/** Media Queries **/
@mixin telefono {
    @media (min-width: #{$telefono}) {
        @content;
    }
}

@mixin tablet {
    @media (min-width: #{$tablet}) {
        @content;
    }
}
  
@mixin desktop {
    @media (min-width: #{$desktop}) {
        @content;
    }
}
@mixin grid($columnas, $espaciado ) {
    display: grid;
    gap: $espaciado;
    grid-template-columns: repeat($columnas, 1fr);
}


@mixin boton($color, $display) {
   background-color: $color;
   color: $blanco;
   font-weight: $bold;
   text-decoration: none;
   padding: 1rem 2rem;
   border: none;
   margin: 2rem;
   cursor: pointer;
   text-align: center;
   margin-top: calc($separacion / 2);
   display: $display;
    border-radius: 15px;

    &:hover{
        background-color: darken($color, 8);
       
    }
}